import Vue from "vue";
import VueRouter from "vue-router";

import HomeForDummy from "../views/HomeForDummy.vue";

Vue.use(VueRouter);

const routes = [
 {
  path: "/",
  name: "Home",
  //
  component: HomeForDummy,
  children: [
   {
    path: "/AboutUs",
    name: "/AboutUs",
    component: () => import("../components/Pages/AboutPage.vue"),
   },
   {
    path: "/ContactUs",
    name: "/ContactUs",
    component: () => import("../components/Pages/ContactUs.vue"),
   },
   {
    path: "/Projects",
    name: "/Projects",
    component: () => import("../components/Pages/ProjectsPage.vue"),
   },
   {
    path: "/Services",
    name: "/Services",
    component: () => import("../components/Pages/ServicesPage.vue"),
   },
  ],
 },
];

const router = new VueRouter({
 mode: "history",
 base: process.env.BASE_URL,
 routes,
});

export default router;
